import React from 'react';
import PropTypes from 'prop-types';
import { color, util } from 'public/styles';

const blcIconBase = props => (
	<i>
		{props.children}

		<style jsx>{`
			color: ${
	props.inverted
		? (props.color
			? props.color
			: color.white)
		: (props.color
			? props.color
			: color.asphalt)
	};
			display: ${props.display};
			height: ${util.blcIconSize(props.size)};
			margin: ${props.margin};
			text-align: ${props.textAlign};
			width: ${util.blcIconSize(props.size)};
		`}</style>
	</i>
);

blcIconBase.propTypes = {
	children: PropTypes.node,
	color: PropTypes.string,
	display: PropTypes.oneOf(['inline', 'inline-block', 'block']),
	inverted: PropTypes.bool,
	margin: PropTypes.string,
	size: PropTypes.oneOf([null, 'largest', 'larger', 'smaller', 'smallest', 'inherit']),
	textAlign: PropTypes.oneOf(['left', 'center', 'right'])
}

blcIconBase.defaultProps = {
	children: null,
	display: 'inline-block',
	inverted: false,
	margin: '0',
	size: null,
	textAlign: 'center'
}

export default blcIconBase;
